import React from 'react';
import PropTypes from 'prop-types';

import Button from '../../core/Buttons';

import { StyledModal } from './style';

const ModalLegalWarning = ({ setaceptLegalConditions, onAceptLegal }) => (
  <StyledModal title="PROTECCIN DE DATOS">
    <p className="legal-text">
      Queremos informarte de que el Grupo Atresmedia cuenta con un Modelo de
      Cumplimiento de la normativa de proteccin de datos, que incluye una serie
      de polticas, protocolos y herramientas de seguimiento y control, que
      recogen los objetivos del Grupo en esta materia y los principios que deben
      regir sus actuaciones, las de sus empleados y colaboradores.
    </p>
    <p className="legal-text">
      En la intranet, en el apartado Documentacin relevante puedes consultar
      toda la informacin, completa y actualizada, sobre el modelo de gobierno,
      los rganos que intervienen en l, funciones y competencias, normas
      internas, polticas y protocolos aplicables, los mecanismos establecidos
      para su adecuado funcionamiento as como recursos formativos en
      privacidad.
    </p>
    <p className="legal-text">
      Te comunicamos que adems el Grupo cuenta con una{' '}
      <strong>Delegada de Proteccin de Datos (DPO)</strong> que vela por el
      cumplimiento de la normativa y con la que se debe consultar cualquier
      aspecto relacionado con la privacidad de los datos y su tratamiento a
      travs del correo electrnico:{' '}
      <a href="privacidad@atresmedia.com">privacidad@atresmedia.com</a>{' '}
    </p>
    <div className="center">
      <Button
        className="legal-acept"
        dark
        onClick={() => {
          onAceptLegal();
          setaceptLegalConditions(true);
        }}
      >
        Aceptar
      </Button>
    </div>
  </StyledModal>
);

ModalLegalWarning.propTypes = {
  setaceptLegalConditions: PropTypes.func,
  onAceptLegal: PropTypes.func
};

export default ModalLegalWarning;
