import React from 'react';
import storiesOf from '../../../../.storybook/util';
import EditionIRPF from '.';
import {
  situation,
  geografic,
  laboral,
  pay
} from '../../../utils/mocks/js/irpf';

storiesOf('core/EditionIRPF', module)
  .add('Situacin Familiar', () => (
    <EditionIRPF title="Situacin Familiar" dataList={situation} />
  ))
  .add('Movilidad geogrfica', () => (
    <EditionIRPF title="Movilidad geogrfica" dataList={geografic} />
  ))
  .add('Prolongacin de actividad laboral', () => (
    <EditionIRPF title="Prolongacin de actividad laboral" dataList={laboral} />
  ))
  .add(
    'Pagos por la adquisicin o rehabilitacin de la vivienda habitual utilizando financiacin ajena, con derecho a deduccin en el IRPF',
    () => (
      <EditionIRPF
        title="Pagos por la adquisicin o rehabilitacin de la vivienda habitual utilizando financiacin ajena, con derecho a deduccin en el IRPF"
        dataList={pay}
      />
    )
  )
  .add('con InputDate', () => (
    <EditionIRPF
      title="Con InputDate"
      dataList={[{ labelPepe: 'Fecha y hora' }]}
    />
  ));
